html,
body {
  height: 100%;
}

body {
  font: 14px "Circe", sans-serif;
  color: $main;
}

* {
  outline-style: none;
  outline-width: 0px !important;
  outline-color: none !important;
}

h1,
.h1 {
  font-size: 35px;
  font-weight: 900;
  margin-bottom: 34px;
}

h3,
.h3 {
  font-size: 35px;
  font-weight: 900;
}

h4,
.h4 {
  font-size: 21px;
  font-weight: 900;
  &-lg {
    font-size: 29px;
  }
}

@media (min-width: 1300px) {
  .container {
    max-width: 1250px;
  }
}

a {
  @include trs(0.2s, 0s);
}

.form-control::placeholder {
  color: #c4c7ca;
}

.main {
  position: relative;
  padding-top: 130px;
  padding-left: 260px;
  min-height: 100%;
}

.content {
  background: #fff;
  min-height: 100%;
  padding: 40px;
  display: flex;
  flex: 1;
  flex-direction: column;
  &-text {
    font-size: 18px;
    line-height: 25px;
  }
}

.footer {
  background: #2c2d2d;
  padding: 50px 0;
  margin-top: auto;
  &-box-info {
    color: #fff;
    font-size: 21px;
    font-weight: 400;
  }
}

.soc {
  div {
    padding: 0 20px;
  }
}

.b-lure {
  margin-bottom: 80px;
}

.title-lg {
  font-size: 72px;
  font-weight: 300;
}

.ibox {
  border: 1px solid #dadada;
  background: #f5f5f5;
  border-radius: 3px;
  &-collapse {
    .ibox-content {
      display: none;
    }
  }
  &-header {
    padding: 14px 13px;
    display: flex;
    align-items: flex-start;
    &-info {
      width: 100%;
      display: flex;
      align-items: center;
    }
  }

  &-tools {
    @include trs(0.2s, 0s);
    border-radius: 3px;
    cursor: pointer;

    .icon-angle {
      opacity: 0;
      @include trs(0.2s, 0s);
    }
    &:hover {
      background: #e6e6e6;
      .icon-angle {
        opacity: 1;
      }
    }
    &-icons {
      display: flex;
      align-items: center;
      padding: 10px;
      &:after {
        display: none;
      }
      .icon-dots {
        margin-left: 14px;
      }
    }

    .dropdown {
      &-menu {
        padding: 0;
        min-width: auto;
        border: none;
        border: none;
        background: #e6e6e6;
        overflow: hidden;
      }
      &-item {
        font-size: 16px;
        padding: 8px 10px;
        line-height: auto;
        line-height: 16px;
        color: $main;
        @include trs(0.2s, 0s);
        &:hover {
          background: $warning;
        }
      }
    }
  }
}

.b-dashboard {
  margin-bottom: 80px;

  &-list {
    width: auto;
    overflow: auto;
    margin: 0 -40px 20px -40px;
    padding: 0 15px;
    .owl-stage {
      margin: 0 auto;
    }
    .owl-dots {
      margin-top: 20px;
    }
  }
}

.feed {
  &-element {
    display: flex;
    align-items: center;
    padding: 14px 13px;
    border-top: 2px solid #fff;
  }
}

.link-history {
  text-decoration: underline;
  font-size: 26px;
  color: $main;
  &:hover {
    text-decoration: none;
    color: $warning;
  }
}

.b-questions {
  font-size: 21px;
  line-height: 30px;
  margin-bottom: 70px;
  p {
    margin-bottom: 30px;
  }

  &-item {
    margin-bottom: 62px;
    &-header {
      cursor: pointer;
      div {
        display: inline-block;
        border-bottom: 2px dashed transparent;
      }
      &.collapsed {
        div {
          border-color: $warning;
        }
      }
    }
  }
}

.questions-route {
  text-align: left;
  position: relative;
  span {
    position: absolute;
    right: 0;
    top: 50%;
    margin-top: -13px;
    display: block;
    padding: 0 15px;
  }
}

.dba-link {
  a {
    color: $main;
    display: inline-block;
    text-decoration: none;
    border-bottom: 2px dashed $warning;
    &:hover {
      color: $warning;
    }
  }
}

.chat {
  &-new-message {
    font-weight: 700;
  }
  &-list {
    overflow: auto;
    height: 100%;
  }
  &-item {
    padding: 22px 10px;
    display: flex;
    align-items: center;
    cursor: pointer;
    @include trs(0.2s, 0s);
    border-bottom: 1px solid #cccccc;
    &:hover,
    &.active {
      background: #f2f2f2;
    }
    &.active {
      cursor: default;
    }
  }
  &-icon {
    width: 35px;
    height: 35px;
    background: $warning;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    .sum-noti {
      position: absolute;
      left: 0;
      bottom: -23px;
      top: auto;
      width: 100%;
      text-align: center;
      background: none;
      display: none;
    }
  }
  &-info {
    padding-left: 9px;
    width: 100%;
    position: relative;
    .sum-noti {
      top: 20px;
    }
  }
  &-title {
    font-size: 16px;
    font-weight: 900;
  }
  &-poster {
    font-size: 14px;

    white-space: nowrap;
    text-overflow: ellipsis;
    width: 100%;
    overflow: hidden;
    max-width: 200px;
  }

  &-date {
    font-size: 11px;
    color: #b3b3b3;
    white-space: nowrap;
  }
}

.msng {
  display: flex;
  flex: 1;
  height: 560px;
  max-height: 560px;
  min-height: 560px;
  &-lg {
    max-height: 800px;
    min-height: 800px;
    .msng-chat-space {
      padding-bottom: 0;
    }
  }
  &-title {
    font-size: 16px;
    font-weight: 900;
  }
  &-bar {
    position: relative;
    padding: 50px 0 80px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    background: #fff;
    @include trs(0.2s, 0s);
    display: flex;
    flex-direction: column;
    &-space {
      width: 300px;
      height: 100%;
    }
    .msng-footer {
      padding-left: 50px;
      padding-right: 50px;
    }
  }
  &-header {
    background: #e8e9e9;
    border-radius: 10px 10px 0 0;
    height: 50px;
    padding: 13px 15px;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 15;
    &-tools {
      position: absolute;
      left: 0;
      top: 0;
      padding: 14px;
      &-right {
        left: auto;
        right: 0;
      }
      &-icon {
        padding: 0 8px;
        span {
          cursor: pointer;
        }
      }
    }

    .ibox-tools {
      position: absolute;
      right: 10px;
      top: 2px;
    }
  }
  &-footer {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 80px;
    border-radius: 0 0 10px 10px;
    padding: 20px 15px;
    border-top: 1px solid #cccccc;
    background: #fff;
    &-bg {
      background: #e8e9e9;
    }

    &-not-abs {
      position: relative;
      height: auto;
    }
  }

  &-chat {
    width: 100%;
    padding-left: 16px;
    &-space {
      position: relative;
      display: flex;
      height: 100%;
      flex-direction: column;
      padding: 50px 0 80px;
      box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
      border-radius: 10px;
      background: #fff;
    }
  }

  &-list {
    overflow: auto;
    padding: 20px 34px;
  }

  &-scroll-to-bottom {
    display: block;
    position: absolute;
    right: 20px;
    background: #fff;
    z-index: 20;
    bottom: 100%;
    margin-bottom: 10px;
    width: 47px;
    height: 47px;
    border-radius: 50%;
    border: 1px solid #f2f2f2;
    box-shadow: 0 5px rgba(242, 242, 242, 0.5);
    opacity: 0;
    @include trs(0.2s, 0s);
    cursor: pointer;
    pointer-events: none;
    &.open {
      opacity: 1;
      pointer-events: painted;
    }
    .sum-noti {
      left: 50%;
      top: -12px;
      margin-left: -12px;
    }
  }
}

.word-search {
  position: relative;
  &-icon {
    position: absolute;
    left: 0;
    top: 0;
    width: 30px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0.5;
    transform: scale(0.8);
  }
  &-input {
    height: 40px;
    border: 1px solid #bfbfbf;
    border-radius: 20px;
    padding: 10px 10px 10px 30px;
    font-size: 14px;
    color: $main;
    font-weight: 300;
    &::-webkit-input-placeholder {
      opacity: 1;
      color: #999999;
    }
    &::-moz-placeholder {
      opacity: 1;
      color: #999999;
    }
    &:-ms-input-placeholder {
      opacity: 1;
      color: #999999;
    }
    &:-moz-placeholder {
      opacity: 1;
      color: #999999;
    }
  }
}

.date-search {
  position: absolute;
  right: 15px;
  top: 50%;
  margin-top: -11px;
  cursor: pointer;
  .calendar {
    position: absolute;
    bottom: 100%;
    right: 0;
    margin-bottom: 10px;
    margin-right: -7px;
  }
}

.calendar {
  width: 285px;
  background: #f2f2f2;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  &-header {
    padding: 10px 42px;
    position: relative;
    text-align: center;
    border-bottom: 1px solid #cccccc;
    font-size: 14px;
    text-transform: uppercase;
  }
  &-close {
    position: absolute;
    right: 16px;
    top: 50%;
    margin-top: -6px;
    background: $warning;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    cursor: pointer;
    i {
      display: block;
      margin: 3px 0 0 3px;
    }
  }

  &-footer {
    text-align: center;
    font-size: 14px;
    padding: 10px 20px;
    border-top: 1px solid #cccccc;
    a {
      color: $warning;
      text-decoration: underline;
      &:hover {
        text-decoration: none;
      }
    }
  }
}

.datepickers-container {
  width: 200px;
  .calendar {
    background: #fff;
  }
}

.datepicker {
  width: 100%;
  border: none;
  border-radius: 0;
  font-size: 12px;
  color: $main;
  z-index: 500;
  &--day {
    &-name {
      color: #666666;
      font-weight: 900;
      text-transform: uppercase;
    }
  }

  &--nav {
    &-title {
      font-weight: 900;
    }
  }

  &--cell {
    &.-selected- {
      background: $warning;
      color: $main;
      &.-current- {
        background: $warning;
        color: $main;
      }
      &.-focus- {
        background: $warning;
      }
    }
    &.-in-range- {
      background: $warning;
    }
    &.-range-from- {
      border-radius: 15px 0 0 15px;
    }
    &.-range-to- {
      border-radius: 0 15px 15px 0;
    }
  }
}

.date-wrap {
  .calendar {
    display: none;
    &.open {
      display: block;
    }
  }
}

.search-options {
  position: absolute;
  bottom: 100%;
  left: 10px;
  right: 10px;
  padding: 20px;
  background: #f2f2f2;
  margin-bottom: 10px;
  border-radius: 10px;
  font-size: 12px;
  visibility: hidden;
  opacity: 0;
  transform: translateY(100px);
  @include trs(0.2s, 0s);
  &.open {
    visibility: visible;
    opacity: 1;
    transform: translateY(0);
  }
  &-title {
    font-size: 24px;
  }
  ul {
    margin: 0;
    padding: 0;
    list-style: none;
    line-height: 16px;
    li {
      padding: 6px 0 6px 14px;
      position: relative;
      &:before {
        content: "";
        position: absolute;
        width: 8px;
        height: 8px;
        background: $warning;
        border-radius: 50%;
        left: 0;
        top: 10px;
      }
    }
  }
}

.date-range-clear {
  background: #fcc858;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  cursor: pointer;
  margin-left: 10px;
  i {
    display: block;
    margin: 3px 0 0 3px;
  }
}

.cancle-range-date {
  color: $main;
  text-decoration: none;
  font-size: 14px;
  &:hover {
    color: $warning;
    text-decoration: none;
  }
}

.alert-chat {
  display: inline-block;
  border-radius: 20px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  padding: 8px 20px;
}

.date-start-chat {
  padding: 10px;
  text-align: center;
}

.chat-date {
  font-size: 14px;
  color: #b3b3b3;
  font-weight: 700;
}

.message-item {
  padding: 15px 0 15px 50px;
  &-info {
    margin-bottom: 8px;
    position: relative;
    &-ava {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      border: 2px solid $warning;
      background: $warning;
      overflow: hidden;
      position: absolute;
      left: -50px;
      bottom: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      img {
        max-width: 100%;
      }
    }
    &-text {
      background: #f2f2f2;
      border-radius: 20px 20px 20px 0;
      padding: 15px 25px;
      font-size: 14px;
      line-height: 20px;
      display: inline-block;
      max-width: 80%;
      text-align: left;
    }
  }
  &-name {
    font-size: 16px;
    font-weight: 900;
    margin-bottom: 7px;
  }

  &-outgoing {
    padding-left: 0;
    padding-right: 50px;
    .message-item {
      &-name {
        text-align: right;
      }
      &-info {
        text-align: right;
        &-ava {
          left: auto;
          right: -50px;
        }
        &-text {
          border-radius: 20px 20px 0 20px;
          background: $warning;
        }
      }
    }
    .chat-date {
      text-align: right;
    }
  }
}

.message {
  &-separate {
    display: flex;
    align-items: center;
    white-space: nowrap;
    font-weight: 700;
    color: #b3b3b3;
    span {
      width: 100%;
      height: 2px;
      background: #d9d9d9;
    }
    div {
      padding: 0 24px;
    }
  }
}

.msng {
  &-clip {
    &-addfile {
      width: 50px;
      height: 40px;
      position: relative;
      overflow: hidden;
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;
      input[type="file"] {
        height: 100%;
        width: 100%;
        position: absolute;
        left: 0;
        top: 0;
        z-index: 15;
        opacity: 0;
      }
    }
  }

  &-area {
    width: 100%;
    input,
    textarea {
      max-height: 300px;
      resize: none;
      padding: 10px 32px;
      border: 1px solid #bfbfbf;
      border-radius: 20px;
      height: 40px;
      background: #fafafa;
      &::-webkit-input-placeholder {
        color: #999999;
      }
      &::-moz-placeholder {
        color: #999999;
      }
      &:-ms-input-placeholder {
        color: #999999;
      }
      &:-moz-placeholder {
        color: #999999;
      }

      &:focus {
        color: #495057;
        background-color: #fff;
        border-color: $warning;
        outline: 0;
        outline-width: 0px;
        box-shadow: 0 0 0 0.2rem rgba(252, 200, 88, 0.25);
      }
    }
  }
}

.btn-send {
  display: block;
  width: 40px;
  height: 39px;
  border-radius: 50%;
  margin: 0 23px;
  padding: 7px 5px 5px 9px;
  border: none;
  display: flex;
  background: none;
  @include trs(0.1s, 0s);
  &:active {
    background: #ebeaea;
  }
}

.text-select {
  background: #2e2e2e;
  color: #fff;
  padding: 0 4px;
  border-radius: 2px;
}

.js-toggle-chat-bar {
  display: none;
  cursor: pointer;
  text-align: center;
  padding: 3px;
}

.msng {
  &-chat {
    &-search {
      padding: 10px 24px;
      background: #e8e9e9;
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      border-top: 1px solid #cccccc;
      display: none;
      &.open {
        display: block;
      }
      &-area {
        background: #fff;
        border: 1px solid #bfbfbf;
        border-radius: 3px;
        display: flex;
        align-items: center;
      }

      &-tools {
        display: flex;
        align-items: center;
        padding: 0 15px;
        &-item {
          padding: 0 10px;
        }
      }

      &-caunt {
        font-size: 14px;
      }

      &-close {
        font-size: 14px;
        font-weight: 700;
        cursor: pointer;
      }

      &-switch {
        display: flex;
        align-items: center;
        &-item {
          cursor: pointer;
          &.disabled {
            cursor: default;
            opacity: 0.5;
          }
        }
        .msng-chat-search-tools-item {
          padding: 0 5px;
        }
      }

      &-input {
        border-color: #fff;
        &:focus {
          border-color: #fff;
          box-shadow: none;
        }
      }
    }
  }

  &-expand {
    .msng {
      &-bar {
        &-space {
          width: 55px;
          overflow: hidden;
        }
        .msng-header {
          &-content {
            display: none !important;
          }
        }

        .msng-footer {
          padding: 10px 15px;
          & > * {
            display: none;
          }
        }
      }
    }

    .js-toggle-chat-bar {
      display: block;
    }

    .chat {
      &-list {
        padding-right: 16px;
        margin-right: -16px;
      }
      &-item {
        margin-right: -16px;
      }
      &-info {
        display: none;
      }
      &-icon {
        .sum-noti {
          display: block;
        }
      }
    }
  }
}

.collaps-link {
  cursor: pointer;
  @include trs(0.2s, 0s);
  &.open {
    transform: rotate(180deg);
  }
}

.doc {
  font-size: 14px;
  text-align: center;
  margin-bottom: 15px;
  &-icon {
    margin-bottom: 7px;
  }
  a {
    color: $main;
    text-decoration: underline;
    &:hover {
      text-decoration: none;
    }
  }
}

.team {
  &-item {
    margin-bottom: 28px;
    &-ava {
      margin-right: 14px;
      border: 1px solid $warning;
      border-radius: 50%;
      overflow: hidden;
      width: 62px;
      height: 62px;
      img {
        max-width: 100%;
        display: block;
      }
      &-lg {
        width: 92px;
        height: 92px;
        padding: 1px;
        border-width: 2px;
      }
    }
    &-name {
      color: #737373;
      font-size: 14px;
    }
  }
}

.opacity-05 {
  opacity: 0.5;
}

.b-info {
  position: relative;
  padding: 15px 0;
  &-numb {
    font-size: 55px;
    line-height: 54px;
    width: 50px;
    position: absolute;
    left: 0;
    top: 0;
  }
  &-body {
    padding-left: 50px;
  }
  &-title {
    font-size: 21px;
    font-weight: 900;
  }
  &-text {
    font-size: 14px;
    line-height: 25px;
    a {
      color: $warning;
      font-weight: 700;
      text-decoration: underline;
      &:hover {
        text-decoration: none;
      }
    }
  }
}

.project {
  &-links {
    font-size: 21px;
    a {
      font-weight: 900;
      color: $main;
    }
    &-separator {
      display: inline-block;
      vertical-align: middle;
      margin: 0 14px;
      width: 1px;
      height: 27px;
      background: #737373;
    }
  }
}

.b-projects {
  &-nav {
    border-bottom: 1px solid #d9d9d9;
    font-size: 14px;
    text-transform: uppercase;
    margin-bottom: 26px;
    .nav {
      &-item {
        margin-left: 52px;
        &:first-child {
          margin-left: 0;
        }
      }
      &-link {
        color: #999999;
        padding: 6px;
        border-bottom: 2px solid transparent;
        margin-bottom: -1px;
        &.active {
          color: $warning;
          border-bottom: 2px solid $warning;
        }
      }
    }
  }
}

.table {
  color: #737373;
  font-size: 16px;

  thead {
    &.table-header-bordered {
      th {
        border-bottom: 1px solid #ebebeb;
      }
    }
    th {
      border: none;
      padding: 14px 22px;
      font-size: 16px;
      font-weight: 700;
    }
  }
  tbody {
    font-weight: 300;
    tr {
      position: relative;
      &:after {
        content: "";
        position: absolute;
        top: 0;
        left: 22px;
        right: 22px;
        height: 1px;
        background: #ebebeb;
      }
      &:first-child {
        &:after {
          display: none;
        }
        td {
          border-top: none;
          &:first-child {
            border-radius: 0.25rem 0 0 0;
          }
          &:last-child {
            border-radius: 0 0.25rem 0 0;
          }
        }
      }
      &:last-child {
        td {
          &:first-child {
            border-radius: 0 0 0 0.25rem;
          }
          &:last-child {
