.burger {
  position: absolute;
  left: 17px;
  top: 50%;
  margin-top: -8px;
  width: 22px;
  display: none;
  span {
    margin: 5px 0;
  }
  span,
  &:before,
  &:after {
    content: "";
    display: block;
    height: 2px;
    border-radius: 2px;
    background: $main;
    position: relative;
    @include trs(0.2s, 0s);
  }
  &.open {
    //transform: rotate(90deg);
    span {
      opacity: 0;
    }
    &:before {
      transform: rotate(45deg);
      bottom: -7px;
    }
    &:after {
      transform: rotate(-45deg);
      top: -7px;
    }
  }
}

