    color: $muted;
    text-decoration: underline;
    strong {
      color: #000;
    }
    &:hover {
      text-decoration: none;
    }
  }
}

.notice-item {
  position: relative;
  padding-right: 14px;
  &:before {
    content: "";
    width: 8px;
    height: 8px;
    border-radius: 50%;
    position: absolute;
    right: 0;
    top: 50%;
    margin-top: -4px;
    background: $warning;
  }
}

.checkbox-support {
  width: 30px;
  height: 16px;
  border-radius: 3px;
  box-shadow: 3px 3px rgba(0, 0, 0, 0.2) inset;
  position: relative;
  cursor: pointer;
  &-switcher {
    display: block;
    width: 14px;
    height: 14px;
    background: #fff;
    border-radius: 3px;
    position: absolute;
    z-index: 10;
    left: 1px;
    top: 1px;
    @include trs(0.2s, 0s);
  }
  &-bg {
    background: $muted;
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    @include trs(0.2s, 0s);
    border-radius: 3px;
  }
  input {
    &:checked + span.checkbox-support-switcher {
      left: 15px;
      & + span.checkbox-support-bg {
        background: $success;
      }
    }
  }
}

.icon-table-protect {
  cursor: pointer;
  &:before {
    content: "";
