    width: 4px;
    border-bottom: 4px solid #737373;
    border-left: 4px solid transparent;
    border-right: 4px solid transparent;
    margin-bottom: 2px;
  }
  &:after {
    content: "";
    display: block;
    width: 4px;
    border-top: 4px solid #737373;
    border-left: 4px solid transparent;
    border-right: 4px solid transparent;
  }
}

