.header {
  background: #e8e9e9;
  padding: 25px 0;
  border-bottom: 1px solid #a9aaab;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  position: fixed;
  z-index: 500;
  top: 0;
  left: 0;
  width: 100%;
}

.modal-open {
  .header {
    padding-right: 15px;
  }
}

.logo {
  width: 230px;
  text-align: center;
}

.urgent-call {
  width: 50px;
  margin: 0 auto;
  &-icon {
    display: block;
    width: 50px;
    height: 50px;
    background: $danger;
    border-radius: 50%;
    box-shadow: 0 0 15px rgba(195, 53, 53, 0.5);
  }
}

.rest {
  font-size: 21px;
  color: $muted;
  white-space: nowrap;
}

.user {
  &-name {
    font-size: 21px;
    white-space: nowrap;
    span {
      display: inline-block;
      vertical-align: middle;
    }
    .icon-profile {
      margin-left: 20px;
    }
  }
}

.small-chats {
  cursor: pointer;
  margin: 0 18px;
}

.user-link {
  display: inline-block;
  position: relative;
}

.user-name {
  .dropdown {
    display: inline-block;
    &-toggle {
      &:after {
        display: none;
      }
    }
  }
}

.sum-noti {
  display: inline-block;
  width: 24px;
  height: 24px;
  position: absolute;
  right: -6px;
  top: -6px;
  background: $warning;
  border-radius: 50%;
  font-size: 14px;
  font-weight: 900;
  text-align: center;
  line-height: 24px;
  &-secondary {
    background: #b3b3b3;
  }
}

.notice {
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
}

.user-link {
  &-item {
    position: relative;
  }
  &-info {
    position: absolute;
    top: 100%;
    right: 0;
    margin-top: 10px;
    display: none;
    &.open {
      display: block;
    }

    .msng-bar,
    .msng-chat {
      width: 630px;
      height: 570px;
    }
  }
}
