
.modal{
    .close{
        position: absolute;
        top: 12px;
        right: 12px;
        cursor: pointer;
    }
    &-body{
        padding: 40px;
        position: relative;
    }
    &-footer{
        padding: 40px;
    }
    
}

.modal-confirm-text{
    font-size: 35px;
    margin-bottom: 28px;
    font-weight: 300;
    span{
        font-weight: 900;
    }
}