@media screen and (max-width: 1199.98px) {
  .footer {
    text-align: center;
    .soc {
      margin: 40px 0;
    }
  }

  .b-initial-form {
    min-height: 100%;
    height: auto;

    .decor {
      display: none;
    }
  }
}

@media screen and (max-width: 991.98px) {
  .content {
    padding: 15px;
  }

  .b-dashboard {
    &-list {
      margin: 0 -15px 20px -15px;
    }
  }

  .user-name .icon-profile {
    margin: 0;
    transform: scale(0.6);
  }

  .user-link {
    transform: scale(0.6);
  }

  .header {
    padding: 15px 0 15px 42px;
  }

  .small-chats {
    margin: 0;
  }

  .burger {
    display: block;
  }

  .main {
    padding-top: 63px;
    padding-left: 0;
  }

  .sidebar {
    top: 0;
    width: 100%;
    z-index: 300;
    height: 100%;
    padding-top: 80px;
    @include trs(0.2s, 0s);
    opacity: 0;
    transform: translateY(-100%);
    visibility: hidden;
    &.open {
      opacity: 1;
      transform: translateY(0);
      visibility: visible;
    }
  }

  .content {
    position: relative;
    z-index: 200;
  }

  .nav-mob-open {
    height: 100%;
    overflow: hidden;
  }

  .mobi-contacts-link {
    display: block;
    opacity: 0;
    transform: translateX(-10px);
    visibility: hidden;
    pointer-events: none;
    @include trs(0.2s, 0s);
    &.active {
      opacity: 1;
      transform: translateX(0);
      visibility: visible;
      pointer-events: initial;
    }
  }

  .nav-mob-open {
    .header-info {
      @include trs(0.2s, 0s);
      opacity: 0;
      visibility: hidden;
      pointer-events: none;
    }
  }

  .b-dba {
    border-top: 1px solid #ebebeb;
    padding: 34px 0;
  }

  .name-project {
    position: relative;
    padding-right: 44px;
    .ibox-tools {
      position: absolute;
      right: 0;
      top: 0;
    }
    &-input {
      input {
        display: block;
        width: 100%;
        font-size: 35px;
      }
    }
  }

  .b-projects {
    &-nav {
      font-size: 11px;
      .nav {
        &-item {
          margin-left: 14px;
        }
        &-link {
          padding: 6px 0;
        }
      }
    }
  }

  .buying-steps {
    margin: -15px -15px 0 -15px;
  }

  .b-profile {
    &-nav {
      font-size: 11px;
      .nav {
        &-item {
          margin-left: 14px;
        }
        &-link {
          padding: 6px 0;
        }
      }
    }
  }

  .rate-item {
    margin-left: 15px;
    .ibox {
      width: 250px;
    }
  }

  .b-consalting {
    &-container {
      width: 100%;
    }
  }

  .consalting-box {
    padding: 20px;
  }

  .urgent-call-icon {
    width: 25px;
    height: 25px;
    .icon-urgent-call {
      @include bg($icon-urgent-call-sm);
    }
  }

  .b-questions {
    &-item {
      margin: 0;
      padding: 34px 0;
      &:first-child {
        border-bottom: 1px solid #ebebeb;
      }
    }
  }
}

@media screen and (max-width: 767.98px) {
  body {
    font-size: 12px;
  }

  h3,
  .h3 {
    font-size: 24px;
  }

  .rest {
    font-size: 14px;
    font-weight: 300;
  }

  .btn {
    font-size: 14px;
    padding: 12px 28px;
  }

  .btn-sm {
    font-size: 11px;
    padding: 5px 14px;
  }

  .content-text {
    font-size: 11px;
    color: #7e8080;
    line-height: 17px;
    text-align: center;
  }

  .b-lure {
    margin-bottom: 22px;
  }

  .title-lg {
    font-size: 36px;
    line-height: 36px;
    margin-bottom: 18px;
  }

  .b-dashboard {
    margin-bottom: 25px;
    &-list {
      padding: 0;
      .row {
        margin: 0;
      }
      .col {
        padding: 0;
        .ibox {
          min-width: 275px;
          margin-right: 15px;
        }
      }
    }
  }

  .link-history {
    font-size: 14px;
  }

  .b-questions {
    margin-bottom: 34px;
    &-icon {
      .icon-questions {
        transform: scale(0.6);
      }
    }
    &-text {
      font-size: 12px;
      line-height: normal;
      p {
        margin-bottom: 16px;
      }
    }
  }

  .questions-route {
    span {
      margin-top: -9px;
    }
  }

  .footer {
    padding: 30px 0;
    &-box {
      &-info {
        font-size: 12px;
      }
    }
    .soc {
      margin: 0;
      div {
        transform: scale(0.5);
      }
    }
  }

  .b-info {
    &-numb {
      font-size: 33px;
      width: 40px;
    }
    &-body {
      padding-left: 40px;
    }
  }

  .bold-text {
    font-size: 14px;
  }

  .table-responsive {
    font-size: 12px;
    display: block;
    text-align: center;
    thead {
      display: none;
    }
    tbody {
      display: block;
    }
    tfoot {
      display: none;
    }
    tr {
      display: block;
      border: 1px solid #dadada;
      border-radius: 5px;
      background: #f5f5f5;
      position: relative;
      margin-bottom: 10px;
      box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
      td {
        display: block;
        padding: 8px;
      }
    }
    &-th {
      display: block;
    }
    .table-tools {
      display: none;
      &-response {
        display: block;
      }
      &-td {
        padding: 0;
      }
    }

    &-title {
      font-size: 24px;
      font-weight: 900;
      text-decoration: underline;
      color: $main !important;
    }
    &.table-hover {
      tbody tr:hover {
        background: #f5f5f5;
      }
    }
    &.shadow,
    .shadow {
      box-shadow: none !important;
    }
  }

  .icon-dots {
    span {
      width: 4px;
      height: 4px;
    }
    &:before,
    &:after {
      width: 4px;
      height: 4px;
    }
  }

  .h1.projects-table-title {
    font-size: 24px;
    text-align: center;
  }

  .tabel-members {
    font-size: 12px;
    thead {
      display: none;
    }
    tbody {
      display: block;
      tr {
        display: block;
        margin-top: 27px;
        &:after {
          display: none;
        }
        td {
          display: block;
          padding: 0;
        }
      }
    }
  }

  .filtered-item * {
    color: $main !important;
  }

  .member-name {
    font-size: 14px;
  }

  .ibox-tools {
    .dropdown-item {
      font-size: 12px;
      padding: 4px 5px;
    }
  }

  .dropdown-item {
    font-size: 12px;
    padding: 4px 5px;
  }

  .modalSShKey-titl {
    div {
      font-size: 24px;
    }
  }

  .modal-body {
    padding: 20px;
  }

  .b-initial-form {
    padding: 32px 0;
    background: $warning;
    &-title {
      margin: 0;
      font-size: 35px;
      margin-bottom: 15px;
    }
    &-essence {
      padding: 0;
    }
    &-subtitle {
      font-size: 14px;
    }
  }

  .form-control-lg {
    font-size: 12px;
    height: 49px;
    padding: 10px 20px;
  }

  .checkbox-lg {
    font-size: 12px;
    line-height: normal;
    padding-left: 34px;
    span {
      width: 24px;
      height: 24px;
      border-width: 1px;
      border-radius: 1px;
      margin-top: -12px;
    }
  }

  .btn-w-md {
    width: auto;
  }

  .buying-steps {
    font-size: 14px;
    padding: 0;
    margin-bottom: 32px;
    ul {
      padding: 14px 0 14px 15px;
      li {
        padding-right: 15px;
        div {
          padding-left: 6px;
          &:before {
            margin-top: -4px;
            border-left-width: 3px;
            border-top-width: 3px;
            border-bottom-width: 3px;
          }
        }
      }
    }
  }

  .type-user {
    font-size: 14px;
    span {
      padding: 10px;
    }
  }

  .step-buying-form-req {
    &-content {
      padding: 17px;
    }
  }

  .form-group-lg {
    label {
      font-size: 12px;
    }
  }

  .step-buying-form-req {
    border: none;
    border-radius: 0;
    margin: 0 -15px;
    box-shadow: none;
    background: #fff;
    &-header {
      border-radius: 0;
    }
    &-content {
      height: auto;
    }
  }

  .buying-detail {
    border: none;
    border-radius: 0;
    padding: 0;
    max-height: none;
    background: #fff;
    box-shadow: none;
  }

  .msng {
    display: block;
  }

  .fly-msng {
    .message-item {
      &-info-text {
        max-width: 100%;
      }
    }
    .msng {
      &-list {
        padding: 20px;
      }
      &-header {
        border-radius: 0;
      }
      &-bar {
        border-radius: 0;
        box-shadow: none;
        &-space {
          width: 100%;
        }
      }
      &-footer {
        border-radius: 0;
        padding: 20px 10px;
      }
      &-chat-space {
        border-radius: 0;
        box-shadow: none;
      }
    }
  }

  .quick-support-container {
    margin: 0;
  }

  .b-issue-area-item {
    padding: 15px;
  }

  .owl-item {
    .pricing-plan {
      opacity: 1;
    }
  }

  .pricing-carousel {
    .owl-dots {
      position: static;
    }
    .owl-stage-outer {
      padding: 0;
      margin-bottom: 15px;
    }
  }

  .pricing-plan {
    border-radius: 0;
    padding: 40px 15px;
  }

  .pricing-price span {
    font-size: 40px;
  }

  .consalting-box {
    .title-lg {
      font-size: 30px;
    }
    .h4-lg {
      font-size: 16px;
    }
  }

  .b-consalting {
    font-size: 16px;
  }

  .consalting-list li {
    margin-bottom: 15px;
  }

  .b-dashboard {
    &-list {
      padding: 0 15px;
    }
  }

  .open-chat-mobi {
    height: 100%;
    overflow: hidden;
  }

  .user-link-info {
    position: fixed;
    z-index: 500;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
    margin: 0;
  }

  .quick-support {
    width: 100%;
    height: 100%;
    &-inside {
      width: 100%;
      height: 100%;
    }

    &-box {
      padding: 14px 12px;
      border-radius: 3px;

      .team-item-ava-lg {
        width: 48px;
        height: 48px;
        img {
          width: 100%;
          height: 100%;
        }
      }

      .user-projext-link {
        padding: 16px 20px;
        .icon-send-dark {
          margin-top: -17px;
        }
      }
    }
  }

  .msng-header {
    border-radius: 0;
  }

  .msng-footer {
    border-radius: 0;
  }

  .msng-chat-search {
    padding: 10px;
  }

  .msng-list {
    padding: 15px;
  }

  .message-item-info-text {
    max-width: 100%;
  }

  .btn-send {
    margin-right: 0;
    margin-left: 4px;
  }

  .msng-area {
    input {
      padding: 10px 15px;
    }
  }

  .msng-chat {
    padding-left: 0;
    height: 100%;
  }

  .user-link-info {
    .msng {
      &-bar,
      &-chat {
        width: 100%;
        height: 100%;
      }
      &-chat {
        &-space {
          border-radius: 0;
        }
      }
    }
  }

  .msng-bar-space {
    width: 100%;
  }

  .msng-lg {
    max-height: inherit !important;
  }

  .bg-404,
  .bg-500 {
    background: none;
  }

  .violation-logo {
    display: none;
  }

  .bg-violation {
    background: none;
  }

  .violation-title {
    font-size: 30px;
  }

  .old-link-title {
    font-size: 30px;
  }
}

@media screen and (max-width: 575.98px) {
}
