
.sidebar{    
    width: 260px;     
    position: fixed;
    left: 0;    
    overflow: auto;
    padding: 40px 0;
    height: calc(100vh - 130px);
    background: $grey;
}

.separator{
    height: 1px;
    background: $main;
    margin: 15px 0;    
}

.box-nav{
    padding: 0 15px;
    text-align: center;
    ul{
        margin: 0;
        list-style: none;
        padding: 0;
        font-size: 14px;        
        li{
            padding: 5px;
            a{
                color: $muted;
                display: inline-block;
                border-bottom: 2px solid transparent;
                &:hover{
                    border-color: $warning;
                    text-decoration: none;
                }
            }
            &.active{
                a{
                    border-color: $warning;
                }
            }
        }
    }
}

.sidebar-project{
    &-text{
        font-size: 16px;        
    }
    &-name{
        font-size: 21px;
        font-weight: 700;
        a{
            color: $main;
        }
    }
}

.js-toggle-list-projects{
    cursor: pointer;
}

.js-toggle-list-projects.open{
    transform: rotate(-134deg);
}